.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#playlist ul{
  list-style: none;
  cursor: pointer;
  padding: 10px;
}
#playlist ul li{
  padding: 10px;
  font-style: oblique ;
}
#playlist ul li:hover{
  color: #007cf0;
}

.container input{
  font-family: 'Times New Roman', Times, serif;
  font-size: 16px;
}

.container{
  width: 500px;
  height: 80vh;
  margin: 100px auto;
  background: rgba(0, 0, 0, 0.1);
  background-size: cover;
  padding: 10px;
  border-radius: 10px;
}
.upload{
  color: rgb(12, 137, 240);
}